         <el-col :span="12">
           <el-form-item v-if="flag=='fabuyonghu'"  label='' prop="fabuyonghuPhone">
               <el-input v-model="ruleForm.fabuyonghuPhone"  placeholder='' clearable></el-input>
           </el-form-item>
