            <el-button v-if="isAuth('renwuOrder','') && scope.row.renwuOrderTypes == 101 && sessionTable=='jiequyonghu' && userId==scope.row.jiequyonghuId" type="primary" icon="el-icon-sold-out" size="mini" @click="refund(scope.row.id)"></el-button>
            <el-button v-if="isAuth('renwuOrder','') && scope.row.renwuOrderTypes == 103 && sessionTable=='jiequyonghu' && userId==scope.row.jiequyonghuId" type="primary" icon="el-icon-reading" size="mini" @click="openCommentModel(scope.row.id)"></el-button>
            <el-button v-if="isAuth('renwuOrder','') && scope.row.renwuOrderTypes == 101 && sessionTable=='fabuyonghu' && userId==scope.row.renwuFabuyonghuId" type="primary" icon="el-icon-reading" size="mini" @click="deliver(scope.row.id)"></el-button>
                          <el-button v-if="isAuth('renwuOrder','') && scope.row.renwuOrderTypes == 101 && sessionTable=='fabuyonghu' && userId==scope.row.jiequyonghuId" type="primary" icon="el-icon-sold-out" size="mini" @click="refund(scope.row.id)"></el-button>


                          <el-button v-if="isAuth('renwuOrder','') && scope.row.renwuOrderTypes == 103
&& sessionTable=='fabuyonghu' && userId==scope.row.renwuFabuyonghuId" type="primary" icon="el-icon-reading" size="mini"
