</template>
<script>
    import AddOrUpdate from "./add-or-update";
    import styleJs from "../../../utils/style.js";
    import utilsJs, {getYearFormat,getMonthFormat,getDateFormat,getDatetimeFormat} from "../../../utils/utils.js";

    export default {
        data() {
        return {
            //
            printFrom: {
                name1:"",
            },
            printVisiable: false,
            //
            searchForm: {
                key: ""
            },
            sessionTable : "",//
            role : "",//
            userId:"",//id
    //
              renwuTypesSelectSearch : [],
    //
              renwuOrderTypesSelectSearch : [],
            form:{
                id : null,
                renwuOrderUuidNumber : null,
                renwuId : null,
                jiequyonghuId : null,
                renwuOrderTruePrice : null,
                renwuOrderTypes : null,
                insertTime : null,
                createTime : null,
            },
            dataList: [],
            pageIndex: 1,
            pageSize: 10,
            totalPage: 0,
            dataListLoading: false,
            dataListSelections: [],
            showFlag: true,
            sfshVisiable: false,
            shForm: {},
            chartVisiable: false,
            echartsDate: new Date(),//echarts
            addOrUpdateFlag:false,
            contents:null,
            layouts: '',

            //excel
            json_fields: {
                //
                     '': 'jiequyonghuName',
                     '': 'jiequyonghuPhone',
                     '': 'jiequyonghuIdNumber',
                     '': 'jiequyonghuPhoto',
                     '': 'newMoney',
                     '()': 'wanchenglv',
                     '': 'jiequyonghuEmail',
                     '': 'renwuName',
                     '': 'renwuUuidNumber',
                     '': 'renwuPhoto',
                     '': 'renwuFile',
                     '': 'zhixingTime',
                     '': 'renwuAddress',
                     '': 'renwuTypes',
                     '': 'renwuJine',
                     '': 'renwuClicknum',
                     '': 'renwuZhuangtaiTypes',
                //
                     '': "renwuOrderUuidNumber",
                     '': "renwuOrderTruePrice",
                     '': "renwuOrderValue",
                     '': "insertTime",
            },

        //
			//id
			commentbackId:null,
			//
			commentbackContent:null,
			//
			commentbackVisible:false,
			//
      renwuCommentbackPingfenNumber:0,
            };
        },
        created() {
            this.contents = styleJs.listStyle();
            this.init();
            this.getDataList();
            this.contentStyleChange()
        },
        mounted() {
            //
            this.sessionTable = this.$storage.get("sessionTable");
            this.role = this.$storage.get("role");
            this.userId = this.$storage.get("userId");

        },
        filters: {
            htmlfilter: function (val) {
                return val.replace(/<[^>]*>/g).replace(/undefined/g,'');
            }
        },
        components: {
            AddOrUpdate,
        },
        computed: {
            getActionUrl: function() {
                return `/`+this.$base.name+`/file/upload`;
            }
        },
        methods: {
            chartDialog() {
                let _this = this;
                let params = {
                    dateFormat :"%Y", //%Y-%m
                    riqi :getYearFormat(_this.echartsDate),//
                    // riqi :getMonthFormat(_this.echartsDate),//
                    thisTable : {//
                        tableName :'renwu_order',//,
                        sumColum : 'renwu_order_number', //
                        date : 'insert_time',//
                        // string : 'renwu_order_name',//
                        // types : 'renwu_order_types',//
                    },
                }
                _this.chartVisiable = true;
                _this.$nextTick(() => {
                    var statistic = this.$echarts.init(document.getElementById("statistic"), 'macarons');
                    this.$http({
                        url: "barSum",//barCountOne barCountTwo barSumOne barSumTwo
                        method: "get",
                        params: params
                    }).then(({data}) => {
                        if(data && data.code === 0){
                            let yAxisName = "";//y
                            let xAxisName = "";//x
                            let series = [];//
                            data.data.yAxis.forEach(function (item,index) {//
                                let tempMap = {};
                                tempMap.name=data.data.legend[index];
                                tempMap.type='bar';
                                tempMap.data=item;
                                series.push(tempMap);
                            })

                            var option = {
                                tooltip: {
                                    trigger: 'axis',
                                    axisPointer: {
                                        type: 'cross',
                                        crossStyle: {
                                            color: '#999'
                                        }
                                    }
                                },
                                toolbox: {
                                    feature: {
                                        // dataView: { show: true, readOnly: false },  // 
                                        magicType: { show: true, type: ['line', 'bar'] },//
                                        // restore: { show: true }, // 
                                        saveAsImage: { show: true }//
                                    }
                                },
                                legend: {
                                    data: data.data.legend//  
                                },
                                xAxis: [
                                    {
                                        type: 'category',
                                        axisLabel:{interval: 0},
                                        name: xAxisName,
                                        data: data.data.xAxis,
                                        axisPointer: {
                                            type: 'shadow'
                                        }
                                    }
                                ],
                                yAxis: [
                                    {
                                        type: 'value',//
                                        name: yAxisName,//y
                                        axisLabel: {
                                            formatter: '{value}' // 
                                        }
                                    }
                                ],
                                series:series//
                            };
                            statistic.setOption(option,true);
                            window.onresize = function () {
                                statistic.resize();
                            };
                        }else {
                            this.$message({
                                message: "",
                                type: "success",
                                duration: 1500,
                                onClose: () => {
                                    this.search();
                                }
                            });
                        }
                    });
                });
            },
            contentStyleChange() {
                this.contentSearchStyleChange()
                this.contentBtnAdAllStyleChange()
                this.contentSearchBtnStyleChange()
                this.contentTableBtnStyleChange()
                this.contentPageStyleChange()
            },
            contentSearchStyleChange() {
                this.$nextTick(() => {
                    document.querySelectorAll('.form-content .slt .el-input__inner').forEach(el => {
                        let textAlign = 'left'
                        if(this.contents.inputFontPosition == 2)
                            textAlign = 'center'
                            if (this.contents.inputFontPosition == 3) textAlign = 'right'
                                el.style.textAlign = textAlign
                            el.style.height = this.contents.inputHeight
                            el.style.lineHeight = this.contents.inputHeight
                            el.style.color = this.contents.inputFontColor
                            el.style.fontSize = this.contents.inputFontSize
                            el.style.borderWidth = this.contents.inputBorderWidth
                            el.style.borderStyle = this.contents.inputBorderStyle
                            el.style.borderColor = this.contents.inputBorderColor
                            el.style.borderRadius = this.contents.inputBorderRadius
                            el.style.backgroundColor = this.contents.inputBgColor
                    })
                    if (this.contents.inputTitle) {
                        document.querySelectorAll('.form-content .slt .el-form-item__label').forEach(el => {
                            el.style.color = this.contents.inputTitleColor
                            el.style.fontSize = this.contents.inputTitleSize
                            el.style.lineHeight = this.contents.inputHeight
                        })
                    }
                    setTimeout(() => {
                        document.querySelectorAll('.form-content .slt .el-input__prefix').forEach(el => {
                            el.style.color = this.contents.inputIconColor
                            el.style.lineHeight = this.contents.inputHeight
                        })
                        document.querySelectorAll('.form-content .slt .el-input__suffix').forEach(el => {
                            el.style.color = this.contents.inputIconColor
                            el.style.lineHeight = this.contents.inputHeight
                        })
                        document.querySelectorAll('.form-content .slt .el-input__icon').forEach(el => {
                            el.style.lineHeight = this.contents.inputHeight
                        })
                    }, 10 )
                })
            },
            // 
            contentSearchBtnStyleChange() {
                this.$nextTick(() => {
                    document.querySelectorAll('.form-content .slt .el-button--success').forEach(el => {
                        el.style.height = this.contents.searchBtnHeight
                        el.style.color = this.contents.searchBtnFontColor
                        el.style.fontSize = this.contents.searchBtnFontSize
                        el.style.borderWidth = this.contents.searchBtnBorderWidth
                        el.style.borderStyle = this.contents.searchBtnBorderStyle
                        el.style.borderColor = this.contents.searchBtnBorderColor
                        el.style.borderRadius = this.contents.searchBtnBorderRadius
                        el.style.backgroundColor = this.contents.searchBtnBgColor
                    })
                })
            },
            // 
            contentBtnAdAllStyleChange() {
                this.$nextTick(() => {
                    document.querySelectorAll('.form-content .ad .el-button--success').forEach(el => {
                        el.style.height = this.contents.btnAdAllHeight
                        el.style.color = this.contents.btnAdAllAddFontColor
                        el.style.fontSize = this.contents.btnAdAllFontSize
                        el.style.borderWidth = this.contents.btnAdAllBorderWidth
                        el.style.borderStyle = this.contents.btnAdAllBorderStyle
                        el.style.borderColor = this.contents.btnAdAllBorderColor
                        el.style.borderRadius = this.contents.btnAdAllBorderRadius
                        el.style.backgroundColor = this.contents.btnAdAllAddBgColor
                    })
                    document.querySelectorAll('.form-content .ad .el-button--danger').forEach(el => {
                        el.style.height = this.contents.btnAdAllHeight
                        el.style.color = this.contents.btnAdAllDelFontColor
                        el.style.fontSize = this.contents.btnAdAllFontSize
                        el.style.borderWidth = this.contents.btnAdAllBorderWidth
                        el.style.borderStyle = this.contents.btnAdAllBorderStyle
                        el.style.borderColor = this.contents.btnAdAllBorderColor
                        el.style.borderRadius = this.contents.btnAdAllBorderRadius
                        el.style.backgroundColor = this.contents.btnAdAllDelBgColor
                    })
                    document.querySelectorAll('.form-content .ad .el-button--warning').forEach(el => {
                        el.style.height = this.contents.btnAdAllHeight
                        el.style.color = this.contents.btnAdAllWarnFontColor
                        el.style.fontSize = this.contents.btnAdAllFontSize
                        el.style.borderWidth = this.contents.btnAdAllBorderWidth
                        el.style.borderStyle = this.contents.btnAdAllBorderStyle
                        el.style.borderColor = this.contents.btnAdAllBorderColor
                        el.style.borderRadius = this.contents.btnAdAllBorderRadius
                        el.style.backgroundColor = this.contents.btnAdAllWarnBgColor
                    })
                })
            },
            // 
            rowStyle({row, rowIndex}) {
                if (rowIndex % 2 == 1) {
                    if (this.contents.tableStripe) {
                        return {color: this.contents.tableStripeFontColor}
                    }
                } else {
                    return ''
                }
            },
            cellStyle({row, rowIndex}) {
                if (rowIndex % 2 == 1) {
                    if (this.contents.tableStripe) {
                        return {backgroundColor: this.contents.tableStripeBgColor}
                    }
                } else {
                    return ''
                }
            },
            headerRowStyle({row, rowIndex}) {
                return {color: this.contents.tableHeaderFontColor}
            },
            headerCellStyle({row, rowIndex}) {
                return {backgroundColor: this.contents.tableHeaderBgColor}
            },
            // 
            contentTableBtnStyleChange() {

            },
            // 
            contentPageStyleChange() {
                let arr = []
                if (this.contents.pageTotal) arr.push('total')
                if (this.contents.pageSizes) arr.push('sizes')
                if (this.contents.pagePrevNext) {
                    arr.push('prev')
                    if (this.contents.pagePager) arr.push('pager')
                    arr.push('next')
                }
                if (this.contents.pageJumper) arr.push('jumper')
                this.layouts = arr.join()
                this.contents.pageEachNum = 10
            },

            init() {
            },
            search() {
                this.pageIndex = 1;
                this.getDataList();
            },
            // 
            getDataList() {
                this.dataListLoading = true;
                let params = {
                    page: this.pageIndex,
                    limit: this.pageSize,
                    sort: 'id',
                }


                if (this.searchForm.jiequyonghuName!= '' && this.searchForm.jiequyonghuName!= undefined) {
                    params['jiequyonghuName'] = '%' + this.searchForm.jiequyonghuName + '%'
                }

                if (this.searchForm.renwuName!= '' && this.searchForm.renwuName!= undefined) {
                    params['renwuName'] = '%' + this.searchForm.renwuName + '%'
                }

                if (this.searchForm.renwuTypes!= '' && this.searchForm.renwuTypes!= undefined) {
                    params['renwuTypes'] = this.searchForm.renwuTypes
                }

                if (this.searchForm.renwuOrderUuidNumber!= '' && this.searchForm.renwuOrderUuidNumber!= undefined) {
                    params['renwuOrderUuidNumber'] = '%' + this.searchForm.renwuOrderUuidNumber + '%'
                }

                if (this.searchForm.renwuOrderTypes!= '' && this.searchForm.renwuOrderTypes!= undefined) {
                    params['renwuOrderTypes'] = this.searchForm.renwuOrderTypes
                }

                params['renwuOrderDelete'] = 1//  1  2 


                this.$http({
                    url: "renwuOrder/page",
                    method: "get",
                    params: params
                }).then(({data}) => {
                    if(data && data.code === 0){
                        this.dataList = data.data.list;
                        this.totalPage = data.data.total;
                    }else{
                        this.dataList = [];
                        this.totalPage = 0;
                    }
                    this.dataListLoading = false;
                });

                //
                this.$http({
                    url: "dictionary/page?dicCode=renwu_types&page=1&limit=100",
                    method: "get",
                    page: 1,
                    limit: 100,
                }).then(({data}) => {
                    if(data && data.code === 0){
                        this.renwuTypesSelectSearch = data.data.list;
                    }
                });
                //
                //
                this.$http({
                    url: "dictionary/page?dicCode=renwu_order_types&page=1&limit=100",
                    method: "get",
                }).then(({data}) => {
                    if(data && data.code === 0){
                        this.renwuOrderTypesSelectSearch = data.data.list;
                    }
                });
            },
            //
            sizeChangeHandle(val) {
                this.pageSize = val;
                this.pageIndex = 1;
                this.getDataList();
            },
            // 
            currentChangeHandle(val) {
                this.pageIndex = val;
                this.getDataList();
            },
            // 
            selectionChangeHandler(val) {
                this.dataListSelections = val;
            },
            // /
            addOrUpdateHandler(id, type) {
                this.showFlag = false;
                this.addOrUpdateFlag = true;
                this.crossAddOrUpdateFlag = false;
                if (type != 'info') {
                    type = 'else';
                }
                this.$nextTick(() => {
                    this.$refs.addOrUpdate.init(id, type);
                });
            },
            // 
            download(file) {
                window.open(" ${file} ")
            },
            // 
            dayinOpen(item) {
                let _this = this;
                _this.printVisiable = true;
                _this.printFrom=item;
            },
            // 
            printContent() {
                let _this = this;
                _this.$print(_this.$refs.printContent)
                _this.printVisiable = false;
            },
            // 
            deleteHandler(id) {
                var ids = id ? [Number(id)] : this.dataListSelections.map(item => {
                    return Number(item.id);
                });

                this.$confirm(`[${id ? "" : ""}]?`, "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                }).then(() => {
                    this.$http({
                        url: "renwuOrder/delete",
                        method: "post",
                        data: ids
                    }).then(({data}) => {
                        if(data && data.code === 0){
                            this.$message({
                                message: "",
                                type: "success",
                                duration: 1500,
                                onClose: () => {
                                    this.search();
                                }
                            });
                        }else{
                            this.$message.error(data.msg);
                        }
                    });
                });
            },
            // 
            renwuOrderUploadSuccess(data){
                let _this = this;
                _this.$http({
                    url: "renwuOrder/batchInsert?fileName=" + data.file,
                    method: "get"
                }).then(({data}) => {
                    if(data && data.code === 0){
                        _this.$message({
                            message: "",
                            type: "success",
                            duration: 1500,
                            onClose: () => {
                                _this.search();
                            }
                        });
                    }else{
                        _this.$message.error(data.msg);
                    }
                });

            },
            // 
            renwuOrderUploadError(data){
                this.$message.error('');
            },

			//
			refund(id){
				this.$confirm(`?`, "", {
					confirmButtonText: "",
					cancelButtonText: "",
					type: "warning"
				}).then(() => {
					this.$http({
						url: "renwuOrder/refund?id="+id,
						method: "post",
					}).then(({ data }) => {
						if (data && data.code === 0) {
							this.$message({
							message:"",
							type: "success",
							duration: 1500,
								onClose: () => {this.search();}
							});
						} else {
							this.$message.error(data.msg);
						}
					});
				});
			},
			//
			deliver(id){
				let _this = this;
				_this.$confirm(`?`, "", {
					confirmButtonText: "",
					cancelButtonText: "",
					type: "warning"
				}).then(() => {
					_this.$http({
						url: "renwuOrder/deliver?id="+id,
						method: "post",
					}).then(({ data }) => {
						if (data && data.code === 0) {
							_this.$message({
								message:"",
								type: "success",
								duration: 1500,
								onClose: () => {this.search();
								}
							});
						} else {
							_this.$message.error(data.msg);
						}
					});
				});
			},
			//
          yiwancheng(id){
				let _this = this;
				_this.$confirm(`?`, "", {
					confirmButtonText: "",
					cancelButtonText: "",
					type: "warning"
				}).then(() => {
					_this.$http({
						url: "renwuOrder/yiwancheng?id="+id,
						method: "post",
					}).then(({ data }) => {
						if (data && data.code === 0) {
							_this.$message({
								message:"",
								type: "success",
								duration: 1500,
								onClose: () => {this.search();
								}
							});
						} else {
							_this.$message.error(data.msg);
						}
					});
				});
			},
			//
			openCommentModel(id){
				//id
				this.commentbackId = null
				//
				this.commentbackContent = null
				//
				this.commentbackId = id
				//
				this.commentbackVisible = true
			},
			//
			commentback(id){
				var _this = this
				if(this.commentbackContent == null || this.commentbackContent == 0){
					this.$message.error("");
					return false;
				}
				if(this.renwuCommentbackPingfenNumber == null || this.renwuCommentbackPingfenNumber == 0){
					this.$message.error("");
					return false;
				}
				_this.$confirm(`?`, "", {
					confirmButtonText: "",
					cancelButtonText: "",
					type: "warning"
				}).then(() => {
					_this.$http({
						url: "renwuOrder/commentback?id="+_this.commentbackId+"&commentbackText="+_this.commentbackContent+"&renwuCommentbackPingfenNumber="+_this.renwuCommentbackPingfenNumber,
						method: "post",
					}).then(({ data }) => {
						if (data && data.code === 0) {
							_this.$message({
								message:"",
								type: "success",
								duration: 1500,
								onClose: () => {
									_this.search();
									//id
									_this.commentbackId = null
									//
									_this.commentbackContent = null
									//
									_this.commentbackVisible = false
									//
									_this.renwuCommentbackPingfenNumber = null
								}
							});
						} else {
							//id
							_this.commentbackId = null,
							//
							_this.commentbackContent = null,
							//
							_this.commentbackVisible = false,
							//
							_this.renwuCommentbackPingfenNumber = null
							_this.$message.error(data.msg);
						}
					});
				});
			},
            //
            wuyong(id) {
                let _this = this;
                _this.$confirm(`    ?`, "", {confirmButtonText: "",cancelButtonText: "",type: "warning"
                }).then(() => {
                    _this.$http({
                        url: "renwuOrder/update",
                        method: "post",
                        data: {
                            id:id,
//                            renwuOrderTypes:renwuOrderTypes,
                        }
                    }).then(({data}) => {
                        if(data && data.code === 0){
                            _this.$message({message: "",type: "success",duration: 1500,onClose: () => {
                                _this.search();
                            }});
                        }else{
                            _this.$message.error(data.msg);
                        }
                    });
                });
            },
        }
    };
</script>
